<script>
export default {
  name: "Home",
  data() {
    return {
      headerContent: {
        display: "inline"
      },
      showHeaderContent: {
        display: "none"
      },
      bg: ""
    };
  },
  methods: {
    // 设置向下按钮平滑滚动
    iconDownScroll() {
      document
        .getElementById("news")
        .scrollIntoView({ block: "start", behavior: "smooth" });
    },
    // 监听页面滚动触发常驻nav和返回顶部动画
    handleScroll() {
      let scrollTop =
        window.pageXOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;

      if (scrollTop < 690) {
        this.headerContent.display = "inline";
        this.showHeaderContent.display = "none";
        this.$refs.cat.style.bottom = "900px";
      } else {
        this.headerContent.display = "none";
        this.showHeaderContent.display = "inline";
        // console.log(this.$refs.cat.style)
        this.$refs.cat.style.bottom = "100px";
      }
    },
    //返回顶部鼠标事件
    backTop() {
      document
        .getElementById("headerBg")
        .scrollIntoView({ block: "start", behavior: "smooth" });
    }
  },
  computed: {},
  components: {},
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    // 进入时改变bcimg
    let num = Math.floor(Math.random() * 7 + 1);
    // this.$refs.backGroundImage.style.backGround = url(`../assets/theme/${num}.jpg`)
    this.bg = `bg${num}`;
  }
};
</script>

