import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/chat',
    name: 'Chat',
    component: resolve=>(require(['../views/chat.vue'],resolve))
  },
  {
    path: '/resource',
    name: 'Resource',
    component: resolve=>(require(['../views/resource.vue'],resolve))
  },
  {
    path: '/message',
    name: 'Message',
    component: resolve=>(require(['../views/message.vue'],resolve))
  },
  {
    path: '/about',
    name: 'About',
    component: resolve=>(require(['../views/about.vue'],resolve))
  }
]

const router = new VueRouter({
  routes
})

export default router
